<template>
 <div id="map" style="width: 500px;height: 500px;"></div>
</template>

<script>
import AMap from 'AMap'
import AMapUI from 'AMapUI'
export default {

  name: 'HelloWorld',
  data () {
    return {
      geoJsonData: ''
    }
  },
  mounted () {
    this.echartsMap = this.$echarts.init(document.getElementById('map'))
    this.opts = {
      subdistrict: 1, // 
      showbiz: false // 
    }
    this.district = new AMap.DistrictSearch(this.opts)// 
    this.district.search('', (status, result) => {
      if (status == 'complete') {
        console.log(result.districtList[0])
        this.getData(result.districtList[0], 'province', 330000)
      }
    })
  },
  methods: {
    loadMap (mapName, data) {
      if (data) {
        
        this.$echarts.registerMap(mapName, data)// geoJsonecharts
        // echartsoption
        var option = {
          visualMap: {
            type: 'piecewise',
            pieces: [
              {max: 30, label: '', color: '#2c9a42'},
              {min: 30, max: 60, label: '', color: '#d08a00'},
              {min: 60, label: '', color: '#c23c33'}
            ],
            color: '#fff',
            textStyle: {
              color: '#fff'
            },
            visibility: 'off'
          },
          series: [{
            name: '',
            type: 'map',
            roam: false,
            mapType: mapName,
            selectedMode: 'single',
            showLegendSymbol: false,
            visibility: 'off',
            itemStyle: {
              normal: {
                color: '#ccc',
                areaColor: '#fff',
                borderColor: '#fff',
                borderWidth: 0.5,
                label: {
                  show: true,
                  textStyle: {
                    color: 'rgb(249, 249, 249)'
                  }
                }
              },
              emphasis: {
                areaColor: false,
                borderColor: '#fff',
                areaStyle: {
                  color: '#fff'
                },
                label: {
                  show: true,
                  textStyle: {
                    color: 'rgb(249, 249, 249)'
                  }
                }
              }
            },
            data: this.mapData// datacode
          }]
        }
        this.echartsMap.setOption(option)
        this.echartsMap.on('click', this.echartsMapClick)
      }
    },
    echartsMapClick (params) { // 
      if (params.data.level == 'street') return// params.datathis.mapData
      this.cityCode = params.data.cityCode
      // 
      // adcode
      this.district.search(this.cityCode, (status, result) => {
        if (status === 'complete') {
          this.getData(result.districtList[0], params.data.level, this.cityCode)// getData
        }
      })
    },
    getData (data, level, adcode) { // 
      var subList = data.districtList
      if (subList) {
        var curlevel = subList[0].level
        if (curlevel === 'street') { // echarts
          console.log(data.districtList)
          let mapJsonList = this.geoJsonData.features
          let mapJson = {}
          for (let i in mapJsonList) {
            if (mapJsonList[i].properties.name == this.cityName) {
              mapJson.features = [].concat(mapJsonList[i])
            }
          }
          this.mapData = []
          // mapDatacode
          this.mapData.push({name: this.cityName, value: Math.random() * 100, level: curlevel})
          console.log(this.cityName)
          console.log(mapJson)
          this.loadMap(this.cityName, mapJson)
          this.geoJsonData = mapJson
          return
        }

        // 
        this.mapData = []
        for (var i = 0, l = subList.length; i < l; i++) {
          var name = subList[i].name
          var cityCode = subList[i].adcode
          // mapDatacode
          this.mapData.push({
            name: name,
            value: Math.random() * 100,
            cityCode: cityCode,
            level: curlevel
          })
        }
        this.loadMapData(adcode)
      }
    },
    loadMapData (areaCode) {
      AMapUI.loadUI(['geo/DistrictExplorer'], DistrictExplorer => {
        // 
        var districtExplorer = window.districtExplorer = new DistrictExplorer({
          eventSupport: true, // 
          map: this.map
        })

        districtExplorer.loadAreaNode(areaCode, (error, areaNode) => {
          if (error) {
            console.error(error)
            return
          }
          let mapJson = {}
          // geojsonfeatures 
          mapJson.features = areaNode.getSubFeatures()
          this.loadMap(this.cityName, mapJson)
        })
      })
    }
  }
}

</script>

<style>
  .map{
    margin: 0 auto;
    height: 600px;
    width: 800px;
  }
  .anchorBL{display:none;}
</style>



